body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.app {
  padding: 20px;
  background-color: #f5f5f5;
  color: #84d8db;
  min-height: 100vh;
}

.app.dark {
  background-color: #222;
  color: white;
}

.content {
  margin-top: 20px;
}
.bmi-container {
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 16px;
  padding: 2rem;
  margin: 3rem auto;
  max-width: 600px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  color: #333;
  text-align: center;
}

.bmi-input {
  padding: 10px;
  margin: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 45%;
  font-size: 1rem;
}

.bmi-button {
  padding: 10px 20px;
  border: none;
  background-color: #3f51b5;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 1rem;
}

.bmi-button:hover {
  background-color: #303f9f;
}

.bmi-result {
  margin-top: 1rem;
  font-weight: bold;
  font-size: 1.1rem;
}

.bmi-table {
  width: 100%;
  background-color: #f9f9f9;
  border-collapse: collapse;
  margin-top: 2rem;
  font-size: 0.95rem;
}

.bmi-table th {
  background-color: #e0e0e0;
}

.bmi-table td,
.bmi-table th {
  padding: 12px;
  border: 1px solid #ccc;
}

.bmi-table tbody tr:hover {
  background-color: #d6f0ff;
  cursor: pointer;
}

.checklist-container {
  max-width: 600px;
  margin: 3rem auto;
  background: rgba(255, 255, 255, 0.95);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.input-section {
  margin-bottom: 1rem;
}

input[type="text"] {
  padding: 8px;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 60%;
  margin-right: 8px;
}

button {
  padding: 6px 12px;
  margin: 2px;
  border: none;
  background-color: #3f51b5;
  color: white;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #303f9f;
}

.checklist {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
}

.checklist-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8px 0;
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 8px;
}

.navbar-title:hover {
  color: #fca311; /* or any accent color */
  transition: color 0.3s ease;
}

.exercise-container {
  padding: 1rem;
  color: #fff;
}

.exercise-form input,
.exercise-form button {
  margin: 0.5rem;
  padding: 0.5rem;
}

.exercise-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1rem;
}

.exercise-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 10px;
  width: 200px;
  text-align: center;
}

.exercise-card img {
  max-width: 100%;
  height: auto;
  border-radius: 6px;
  margin-top: 0.5rem;
}
.exercise-list {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1rem;
}

.exercise-card {
  background: rgba(255, 255, 255, 0.15);
  padding: 1rem;
  border-radius: 10px;
  width: 200px;
  text-align: center;
  color: #fff;
}

.exercise-card img {
  max-width: 100%;
  height: auto;
  margin-top: 0.5rem;
  border-radius: 6px;
}

/* === Shared Styles === */
input, select, textarea, button {
  font-size: 1rem;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  margin: 5px 0;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

button {
  cursor: pointer;
  font-weight: 600;
  border: none;
}

/* === LIGHT MODE === */
.light input,
.light select,
.light textarea {
  background-color: #fff;
  color: #000;
  border-color: #ccc;
}

.light button {
  background-color: #007bff;
  color: #fff;
}

.light button:hover {
  background-color: #0056b3;
  transform: translateY(-1px);
}

.light .card,
.light .checklist-item {
  background-color: #ffffffcc;
  color: #000;
}

/* === DARK MODE === */
.dark input,
.dark select,
.dark textarea {
  background-color: #222;
  color: #f0f0f0;
  border-color: #555;
}

.dark input:hover,
.dark select:hover,
.dark textarea:hover {
  border-color: #888;
}

.dark button {
  background-color: #444;
  color: #fff;
}

.dark button:hover {
  background-color: #666;
  transform: translateY(-4px);
}

.dark .card,
.dark .checklist-item {
  background-color: #2a2a2a;
  color: #f0f0f0;
}

/* Optional: focus glow effect */
input:focus,
select:focus,
textarea:focus {
  outline: none;
  box-shadow: 0 0 4px rgba(0,123,255,0.4);
}

.dark input:focus,
.dark select:focus,
.dark textarea:focus {
  box-shadow: 0 0 4px rgba(255,255,255,0.15);
}


.login-container {
  max-width: 400px;
  margin: 3rem auto;
  background-color: #ffffffcc;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.login-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.styled-input {
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 1rem;
  background-color: #fefefe;
  transition: background-color 0.3s ease, border 0.3s ease;
}

.styled-input.dark {
  background-color: #2d2d2d;
  color: #fff;
  border: 1px solid #555;
}

.styled-input:hover {
  border-color: #007bff;
}

.styled-button {
  width: 100%;
  padding: 0.75rem;
  background-color: #007bff;
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.styled-button.dark {
  background-color: #555;
}

.styled-button:hover {
  background-color: #0056b3;
}

.checklist-container {
  padding: 20px;
  max-width: 600px;
  margin: auto;
}

.input-section input {
  padding: 5px;
  margin-right: 8px;
}

.input-section button {
  padding: 5px 10px;
  font-size: 0.9rem;
}

.checklist {
  list-style: none;
  padding: 0;
}

.checklist-item {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 10px 0;
  border-bottom: 1px solid #ccc;
  flex-wrap: wrap;
}

.checklist-item button {
  padding: 4px 8px;
  font-size: 0.75rem;
  border: none;
  background-color: #eee;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.checklist-item button:hover {
  background-color: #ddd;
}

.checklist-item input[type="number"] {
  width: 80px;
  padding: 4px;
  font-size: 0.8rem;
}
.small-btn {
  padding: 4px 8px;
  font-size: 0.8rem;
  margin: 2px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  transition: background 0.3s ease;
}

.small-btn.light {
  background-color: #f0f0f0;
  color: #333;
}

.small-btn.dark {
  background-color: #444;
  color: #fff;
}

/* In App.css */
.edit-btn.dark {
  background-color: #b56576;
  color: white;
}

.edit-btn.light {
  background-color: #f28482;
  color: white;
}

.timer-btn.dark {
  background-color: #6d597a;
  color: white;
}

.timer-btn.light {
  background-color: #9d4edd;
  color: white;
}
